ul, li {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: none;
  vertical-align: baseline;
}

.list-unordered {
  list-style: none;
  margin: 0rem 0rem 2.25rem 3rem;
  padding-left: 1.75rem;
}

.list-unordered li {
  line-height: 1.5rem;
  margin-bottom: 0.375rem;
  position: relative;
}

.list-unordered li::before {
  color: #009ede;
  content: '\2022';
  font-size: 1.1em;
  left: -1.1em;
  line-height: 1.05;
  position: absolute;
}
